@tailwind base;
@tailwind components;
@tailwind utilities;

header ul li{
    @apply px-2
}
.bg-primary
{
    background:indigo;
}
.bg-secondary{
    background:rgb(165, 165, 240);
}
.mobile-nav {
    @apply fixed top-10 left-0 bg-gray-800 w-full h-1/4;
}
.mobile-nav li {
    @apply w-full h-full text-center border-b-2;
}
.project-desc{
   @apply absolute left-0 right-0 bottom-0 top-0 bg-primary opacity-0 duration-500 hover:opacity-100 
}
.btn{
    @apply bg-secondary px-5 py-2 font-bold border-2 hover:border-white rounded
}
html{
    scroll-behavior: smooth;
}